.toolbar {
    flex-direction: column;
    position: fixed;
    min-width: 6rem;
    background: #f6f6f7;
    height: 100%;
    justify-content: flex-start;
    z-index: 99999;
    display: flex;
}
.toolbar__logo{
    -webkit-tap-highlight-color: transparent;
    align-self: center;
    text-transform: uppercase;
    font-weight: 900;
    color: #6c757d;
    text-align: center;
    width: 2rem;
    font-size: .5rem;
    order: 1;
    padding-left: auto;
    padding-top: 2rem;
    padding-bottom: 2rem;
}
.toolbar__logo a {
    text-decoration: none;
    color: inherit;
}
.toolbar__logo span {
    color: #4c75f2;
}

.toolbar__navigation {
    padding: 1.5rem;

    font-weight: 600;
    font-size: 12px;
    color: #6c757d;
    cursor: pointer;
    transition: all .2s ease-in-out;
    width: 2rem;
    order: 2;

}